import React, { useMemo } from 'react';
import { Formik } from 'formik'
import score from '../../json/score';

const Provider = ({ children }) => {
  const calculateScore = Object.values(score)

  return (
    <Formik
      initialValues={{ answers: [] }}
      onSubmit={(values) => {
        console.log(values)
        const points = calculateScore.reduce((res, scores, i) => {
          let total
          res.push(total)
          return res
        }, [])
      }}
    >
      {children}
    </Formik>
  )
}

export default Provider;
